import * as React from 'react'
import { graphql } from 'gatsby'
import Layout from '../components/layout'

const NotePage = (props) => (
  <Layout home={false}>
    <div dangerouslySetInnerHTML={{ __html: props?.data?.remark?.html }} />
  </Layout>
)

export const Head = (props) => {
  const { meta, headings, fields } = props?.data?.remark
  const title = meta?.title || headings?.map((h) => h?.value)?.shift() || fields?.path?.replace(/[-_]+/g, ' ')
  return <title>{title}</title>
}

export const query = graphql`
  query GetNoteById($id: String) {
    remark: markdownRemark(id: { eq: $id }) {
      fields {
        path
      }
      meta: frontmatter {
        title
      }
      headings(depth: h1) {
        value
      }
      html
    }
  }
`

export default NotePage
